import React, { useEffect, useRef } from 'react'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger)

const shows = [
  { date: '2025-12-05', place: 'São Paulo - Teatro X', status: 'Disponível', venue: 'Teatro X' },
  { date: '2025-11-20', place: 'Rio de Janeiro - Arena Y', status: 'Esgotado', venue: 'Arena Y' }
]

export default function Shows(){
  const sectionRef = useRef(null)
  const titleRef = useRef(null)
  const cardsRef = useRef([])

  useEffect(() => {
    const ctx = gsap.context(() => {
      if(titleRef.current) {
        gsap.fromTo(titleRef.current,
          { opacity: 0, y: 50, filter: 'blur(10px)' },
          {
            opacity: 1,
            y: 0,
            filter: 'blur(0px)',
            duration: 1,
            ease: 'power3.out',
            scrollTrigger: {
              trigger: titleRef.current,
              start: 'top 85%',
              toggleActions: 'play none none reverse'
            }
          }
        )
      }

      cardsRef.current.forEach((card, idx) => {
        if(card) {
          gsap.fromTo(card,
            { opacity: 0, x: -30, scale: 0.95 },
            {
              opacity: 1,
              x: 0,
              scale: 1,
              duration: 0.6,
              ease: 'power2.out',
              delay: idx * 0.15,
              scrollTrigger: {
                trigger: card,
                start: 'top 85%',
                toggleActions: 'play none none reverse'
              }
            }
          )
        }
      })
    }, sectionRef)

    return () => ctx.revert()
  }, [])

  return (
    <section 
      id="shows" 
      ref={sectionRef}
      className="py-24 md:py-32 relative grain-bg overflow-hidden"
    >
      <div className="container">
        <h2 
          ref={titleRef}
          className="text-4xl md:text-6xl lg:text-7xl font-heading text-gold mb-4 reveal"
        >
          Shows e Agenda
        </h2>
        <p className="text-white/60 text-lg mb-12 max-w-2xl">
          Próximas apresentações e eventos.
        </p>

        <div className="mt-12 grid md:grid-cols-2 gap-6 lg:gap-8">
          {shows.map((s, i) => (
            <div 
              key={i}
              ref={el => cardsRef.current[i] = el}
              className="glass-strong p-6 rounded-xl border border-gold/10 hover:border-gold/30 transition-all duration-300 reveal group"
            >
              <div className="flex items-start justify-between gap-4">
                <div className="flex-1">
                  <div className="text-gold font-heading text-2xl mb-2">
                    {new Date(s.date).toLocaleDateString('pt-BR', { day: 'numeric', month: 'long', year: 'numeric' })}
                  </div>
                  <div className="text-white font-semibold text-lg mb-1">{s.venue}</div>
                  <div className="text-white/60 text-sm">{s.place}</div>
                </div>
                <div className={`px-4 py-2 rounded-full text-sm font-semibold ${
                  s.status === 'Esgotado' 
                    ? 'bg-red/20 text-red-light border border-red/30' 
                    : 'bg-gold/20 text-gold border border-gold/30'
                }`}>
                  {s.status}
                </div>
              </div>
              
              <button className="btn-ghost mt-6 w-full group-hover:border-gold transition-colors">
                Ver Detalhes
              </button>
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}
